import React, { Component } from "react";
import "./App.css";
import SearchBar from "./SearchBar.js";
import MenuBar from "./MenuBar.js";
import PhotoCarousel from "./PhotoCarousel.js";
import NavBar from "./NavBar.js";
import "materialize-css/dist/css/materialize.min.css";
import "./index.css";
import "./Modal.css";
import axios from "axios";

export const DEFAULT_ALBUM = "default";
export const SEARCH_FLAGS = [ "Please login to use this feature.", "No albums found.", "Make sure you're connected to the internet." ];


class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      idtoken: "",
      email: "",
      currAlbum: DEFAULT_ALBUM,
      photos: [],
	  searchStatus: ""
    };
  };

  authCallBack = (data) => {
    this.setState({
      idtoken: data.idtoken,
      email: data.email,
	  searchStatus: ""
    });
  };

  removeAuthCallBack = () => {
	this.setState({
      idtoken: "",
      email: "",
	  searchStatus: ""
	});
  };

  setAlbumCallBack = (album) => {
    //console.log("Album is now: " + album);

    axios
      .get("/albumPhotos", {
        params: { idtoken: this.state.idtoken, albumName: album },
      })
      .then(
        (resolve) => {
          if (resolve.data[0] !== undefined) {
            this.setState({
              currAlbum: album,
              photos: resolve.data[0].album,
			  searchStatus: ""
            });
          } else {
            this.setState({
              currAlbum: DEFAULT_ALBUM,
              photos: [],
			  searchStatus: SEARCH_FLAGS[1]
            });
          }
        },
        (error) => {
		  if (error.response.status === 400) {
			this.setState({
			  searchStatus: SEARCH_FLAGS[0]
			});
		  } else if (error.response.status === 404) {
			this.setState({
			  searchStatus: SEARCH_FLAGS[2]
			});
		  }

          console.log("Album doesn't exist!");
        }
      );
  }

  clearCallback = () => {
	this.setState({
	  searchStatus: ""
	});
  }

  render() {
    return (
      <div className="App" style={{ background: "#FFFAFA" }}>
        <NavBar getAuth={this.authCallBack} removeAuthCallBack={this.removeAuthCallBack}/>
        <br />
        <h1>Stiny's Photos </h1>
        <MenuBar />
        <SearchBar setAlbum={this.setAlbumCallBack} searchStatus={this.state.searchStatus} clearCallback={this.clearCallback}/>
        <PhotoCarousel
          idtoken={this.state.idtoken}
          email={this.state.email}
          album={this.state.currAlbum}
          photos={this.state.photos}
        />
        <div className="rect"></div>
      </div>
    );
  }
}

export default App;

